.security_group {
  padding: .26rem .3rem;

  .top {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .com-add-btn {
    margin-bottom: .12rem;
  }

  table {
    border: 1px solid #D9DBE1;
  }

  .ant-table-tbody>tr>td:first-child,
  .ant-table-thead>tr>th:first-child {
    padding-left: .6rem;
  }

  .link {
    cursor: pointer;

    &:hover {
      text-decoration: underline;
    }
  }
  .ant-pagination{
    padding-left: 0;
    padding-right: 0;
  }
}

/* group_rules */
.group_rules {
  .ant-tabs-nav {
    padding: 0 .3rem;

    &::before {
      border-color: #D9DBE1;
    }
  }
  .top{
    display: flex;
    justify-content: space-between;
  }

  .ant-tabs-tab {
    line-height: 1;
    padding: .27rem 0 .2rem 0;
    font-size: .16rem;
    color: #9696A3;
    margin-right: .4rem;
  }

  .opt-box {
    display: flex;
    padding: .25rem .3rem .3rem;

    .btn {
      line-height: .4rem;
      padding: 0 .16rem;
      border-radius: .04rem;
      cursor: pointer;
      font-size: .14rem;
      margin-right: .1rem;
    }

    .add {
      background: #5E5CE3;
      color: #fff;
    }

    .batchAdd {
      background: #F1F1FE;
      color: #5E5CE3;
    }

    .batchDelete {
      background: #F2F1F6;
      color: #525666;
    }
  }

  .multiple {
    tr {

      td,
      th {
        &:first-child {
          padding-left: .3rem;

          div,
          label {
            position: relative;
            z-index: 10;
          }
        }

        &:nth-child(2) {
          padding-left: .56rem;
        }
      }
    }
  }
  .cloud-tab{
    tr {
      td,
      th {
        &:first-child {
          padding-left: .6rem;
        }
      }
    }
  }
  .relation{
    padding: 0 .3rem .3rem .3rem;
    .top{
      margin-bottom: .12rem;
    }
  }
  .cloud-tab{
    border: 1px solid #D9DBE1;
  }
}
/* cloud-modal */
.cloud-modal{
  .empty-tip.ant-form-item,
  .ant-form-item-label-left{
    font-size: .14rem;
    color: #525666;
    line-height: 1;
    height: auto;
  }
  .empty-tip{
    margin-bottom: .15rem;
    label{
      line-height: 1;
      height: auto;
    }
  }
  .ant-form-item-control-input{
    min-height: auto;
  }
}
/* batch-modal */
.batch-modal{
  width: 6.6rem !important;
  .t-tit{
    margin-bottom: .15rem;
    span{
      display: inline-block;
      margin-right: 4px;
      color: #ff4d4f;
      font-size: 14px;
      font-family: SimSun, sans-serif;
      line-height: 1;
    }
  } 
  p{
    margin-bottom: 0;
  }
  .ant-checkbox-wrapper{
    margin-right: .3rem;
  }
  .ant-checkbox-wrapper + .ant-checkbox-wrapper{
    margin-left: 0;
  }
  #batch{
    .empty-tip{
      margin-bottom: 0;
      &:nth-child(4){
        margin-bottom: .15rem;
      }
    }
  }
}
@primary-color: #5C5EE4;